import React from "react";
//react-router-dom ktphanesinden useNavigate fonksiyonunu ie aktaryoruz.
// Bu fonksiyon, JavaScript ile sayfalar arasnda ynlendirme yapmamz salar.
import { useNavigate } from "react-router-dom";
import "../../styles/Selection.css";
import gorsel from "../../assets/image_3840.webp";

function RollSelection() {
    // useNavigate fonksiyonu sayesinde ynlendirme yapabilmek iin "navigate" deikenini tanmlyoruz.
    const navigate = useNavigate();

    const git = (roll) => {
        navigate(`/login/${roll}`);
    };

    return (
        <div className="roll-selection">
            {/* Sol taraf: yaz ve butonlar */}
            <div className="roll-left">
                <h2>Select Your Login Type</h2>
                <button onClick={() => git("manager")}>Manager</button>
                <button onClick={() => git("employee")}>Employee</button>
                <button onClick={() => git("customer")}>Customer</button>
            </div>

            {/* Sa taraf: grsel */}
            <div className="roll-right">
                <img src={gorsel} alt="Rol Grseli" />
            </div>
        </div>
    );
}

export default RollSelection;
